.Nav{
    width:100vw;
}

.Items{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding:0.5cm;
}

.icon{
    height:1cm;
    width:1cm;
}

.heading{
    font-size: 25px;
    font-weight: bold;
    color: rgb(34, 86, 243);
}

@media only screen and (min-width:250px) and  (max-width:300px){
    .heading{
        font-size: 20px;
    }
}

@media only screen and (max-width:250px){
    .heading{
        font-size: 16px;
    }
}