* {
    margin: 0;
    padding: 0;
}

.Home {
    width: 100vw;
    height: 100vh;
}

.columnParent{
    width:100vw;
}
.column {
    display: flex;
    flex-direction: row;
    column-gap: 2cm;
    row-gap: 1cm;
    padding: 1cm;
    justify-content: center;
}

@media only screen and (min-width:900px) and (max-width:950px) {
    .column {
        column-gap: 1cm;
    }
}

@media only screen and (min-width:850px) and (max-width:900px) {
    .column {
        column-gap: 0.3cm;
    }
}

@media only screen and (max-width:850px) {
    .column {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        column-gap: 0.2cm;
        row-gap: 0.5cm;
        padding: 0.5cm;
    }
}

@media only screen and (max-width:290px){
    .calendarBox{
        display: none;
    }
}