.todo {
    /* width:100vw; */

}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    row-gap: 0.3cm;
    flex-wrap: wrap;

}

.inputBox {
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    flex-wrap: wrap;
}

.unlist {
    list-style: none;
}

.inputText {
    width: 8cm;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* padding: 0.5cm; */
}

.list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.2cm 0.1cm;
    border: thin solid rgb(197, 197, 197);
    border-radius: 6px;
}

.content {
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    align-items: center;
}

.items {
    font-size: 16px;
    /* margin:0.5cm */
    max-width: 8cm;
}

.delete {
    width: 1cm;
    height: 1cm;
}

.add {
    padding: 0.2cm 0.5cm;
    font-size: 18px;
    background-color: rgb(166, 71, 255);
    color: white;
    border: none;
    border-radius: 6px;
}

.add:hover {
    background-color: rgb(117, 0, 226);
    font-weight: bolder;
}

@media only screen and (min-width:400px) and (max-width:490px) {

    .inputText,
    .items {
        width: 6cm;
    }

    .inputText {
        padding: 0.2cm;
    }
}

@media only screen and (min-width:300px) and (max-width:400px) {

    .inputText,
    .items {
        width: 5cm;
    }

    .inputBox {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        row-gap: 0.5cm;
    }

    .inputText {
        padding: 0.2cm;
    }

    .add {
        width: 5cm;
    }
}

@media only screen and (min-width:200px) and (max-width:300px) {

    .inputText,
    .items {
        width: 5cm;
    }

    .inputBox {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        row-gap: 0.3cm;
    }

    .inputText {
        padding: 0.2cm;
    }

    .add {
        width: 5cm;
    }
}